.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  -webkit-transform: scaleY(.01) scaleX(0);
      -ms-transform: scaleY(.01) scaleX(0);
          transform: scaleY(.01) scaleX(0);
  -webkit-animation: unfoldIn 1s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
          animation: unfoldIn 1s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
}

.modal-main {
  position: fixed;
  padding: 20px;
  background: #fff;
  width: 80%;
  height: auto;
  top: 50%;
  left: 50%;
  -webkit-transform: scale(0) translate(-50%,-50%);
      -ms-transform: scale(0) translate(-50%,-50%);
          transform: scale(0) translate(-50%,-50%);
  -webkit-animation: zoomIn .5s .8s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
          animation: zoomIn .5s .8s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
}


.display-block {
  display: block;
}

.display-none {
  display: none;
}

@-webkit-keyframes unfoldIn {
  0% {
    -webkit-transform: scaleY(.005) scaleX(0);
            transform: scaleY(.005) scaleX(0);
  }
  50% {
    -webkit-transform: scaleY(.005) scaleX(1);
            transform: scaleY(.005) scaleX(1);
  }
  100% {
    -webkit-transform: scaleY(1) scaleX(1);
            transform: scaleY(1) scaleX(1);
  }
}

@keyframes unfoldIn {
  0% {
    -webkit-transform: scaleY(.005) scaleX(0);
            transform: scaleY(.005) scaleX(0);
  }
  50% {
    -webkit-transform: scaleY(.005) scaleX(1);
            transform: scaleY(.005) scaleX(1);
  }
  100% {
    -webkit-transform: scaleY(1) scaleX(1);
            transform: scaleY(1) scaleX(1);
  }
}

@-webkit-keyframes zoomIn {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1) translate(-50%,-50%);
            transform: scale(1) translate(-50%,-50%);
  }
}

@keyframes zoomIn {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1) translate(-50%,-50%);
            transform: scale(1) translate(-50%,-50%);
  }
}
.Close {
  color: var(--background);
  outline: 1px solid var(--background);
  font-size: 1em;
  padding: 8px 16px;
  opacity: 0.8;
  margin-left: 10px;
}



